<template>
	<div>
		<button class="mo-button" @click="alert">alert</button>
		<button class="mo-button" @click="confirm">confirm</button>
		<button class="mo-button" @click="toast">toast</button>
	</div>
</template>

<script>
	import Layer from 'mo/layer'
	export default {
		methods : {
			alert () {
				Layer.alert('')
			},
			confirm() {
				Layer.confirm('', 
					() => Layer.toast(''), 
					() => Layer.toast('')
					)
			},
			toast () {
				Layer.toast('2')
			}
		}
	}
</script>


