<template>
	<mo-tabs v-model="tab">
		<mo-tab-pane tab="" name="tab1"></mo-tab-pane>
		<mo-tab-pane tab="" name="tab2"></mo-tab-pane>
		<mo-tab-pane tab="" name="tab3"></mo-tab-pane>
		<mo-tab-pane tab="" name="tab4"></mo-tab-pane>
		<mo-tab-pane tab="" name="tab5" :disabled="true"></mo-tab-pane>
		<mo-tab-pane tab="" name="tab6"></mo-tab-pane>
		<mo-tab-pane tab="" name="tab7"></mo-tab-pane>
	</mo-tabs>
</template>

<script>
	import {MoTabs, MoTabPane} from 'mo/tabs'
	export default {
		name : 'tabs-pane',
		components : {
			MoTabs,
			MoTabPane
		},
		data () {
			return {
				tab : 'tab3'	
			}
		}
	}
</script>


