<template>
	<div>
		<button class="mo-button" @click="modal = true"></button>
		<mo-modal v-model="modal">
			<template slot="title">Modal</template>
			<template slot="body"></template>
			<template slot="buttons">
				<button class="mo-button--link" @click="modal = false"></button>
				<button class="mo-button--primary" @click="modal2 = true"></button>
			</template>
		</mo-modal>
		<mo-modal v-model="modal2" :closeByOverlay="true" :closeByEsc="true">
			<template slot="body">
				
				<p></p>
				<p><code>ESC</code></p>
			</template>
			<template slot="buttons">
				<button class="mo-button" @click="modal2 = false"></button>
			</template>
		</mo-modal>
	</div>
</template>

<script>
	import MoModal from 'mo/modal'
	export default {
		components : {
			MoModal
		},
		data () {
			return {
				modal : false,
				modal2 : false
			}
		}
	}
</script>


