<template>
	<div class="app-scroll">
		<h2 class="demo-name">Tabs</h2>
		<h3 class="demo-title"><i class="mo-icon__lightbulb"></i></h3>
		<panel>
			<h4 class="title" slot="title"></h4>
			<template slot="body">
				<p><code>mo-tabs</code><code>type</code><code>panel</code></p>
				<p>nav<code>tab</code><code>TAB</code></p>
				<demo :code="examplePaneCode">
					<template slot="demo">
						<example-pane></example-pane>
					</template>
				</demo>
			</template>
		</panel>
		<panel>
			<h4 class="title" slot="title"></h4>
