				<p><code>mo-tabs</code><code>type</code><code>slot</code></p>
				<p>navHTML</p>
				<demo :code="exampleSlotCode">
					<template slot="demo">
						<example-slot></example-slot>
					</template>
				</demo>
			</template>
		</panel>
		<panel>
			<h4 class="title" slot="title"></h4>
			<template slot="body">
				<p><code>mo-tabs</code><code>type</code><code>nav</code></p>
				<p><code>navBar</code>html</p>
				<demo :code="exampleNavCode">
					<template slot="demo">
						<example-nav></example-nav>
					</template>
				</demo>
			</template>
		</panel>

		<h3 class="demo-title"><i class="mo-icon__palette"></i></h3>
		<panel>
			<h4 class="title" slot="title"></h4>
			<template slot="body">
				<p><code>mo-tabs</code><code>className</code><code>card</code></p>
				<demo :code="exampleCardCode">
					<template slot="demo">
						<example-card></example-card>
					</template>
				</demo>
			</template>
		</panel>

